// File#: _2_flexi-header
const flexHeader = document.getElementsByClassName('js-f-header');
    if(flexHeader.length > 0) {
      const menuTrigger = flexHeader[0].getElementsByClassName('js-anim-menu-btn')[0];
      const firstFocusableElement = getMenuFirstFocusable();

      // we'll use these to store the node that needs to receive focus when the mobile menu is closed 
      let focusMenu = false;

      menuTrigger.addEventListener('anim-menu-btn-clicked', event => {
        toggleMenuNavigation(event.detail);
      });

      // listen for key events
      window.addEventListener('keyup', event => {
        // listen for esc key
        if( (event.keyCode && event.keyCode == 27) || (event.key && event.key.toLowerCase() == 'escape' )) {
          // close navigation on mobile if open
          if(menuTrigger.getAttribute('aria-expanded') == 'true' && isVisible(menuTrigger)) {
            focusMenu = menuTrigger; // move focus to menu trigger when menu is close
            menuTrigger.click();
          }
        }
        // listen for tab key
        if( (event.keyCode && event.keyCode == 9) || (event.key && event.key.toLowerCase() == 'tab' )) {
          // close navigation on mobile if open when nav loses focus
          if(menuTrigger.getAttribute('aria-expanded') == 'true' && isVisible(menuTrigger) && !document.activeElement.closest('.js-f-header')) menuTrigger.click();
        }
      });

      // listen for resize
      let resizingId = false;
      window.addEventListener('resize', () => {
        clearTimeout(resizingId);
        resizingId = setTimeout(doneResizing, 500);
      });

      function getMenuFirstFocusable() {
        const focusableEle = flexHeader[0].getElementsByClassName('f-header__nav')[0].querySelectorAll('[href], input:not([disabled]), select:not([disabled]), textarea:not([disabled]), button:not([disabled]), iframe, object, embed, [tabindex]:not([tabindex="-1"]), [contenteditable], audio[controls], video[controls], summary');
        let firstFocusable = false;
        for(let i = 0; i < focusableEle.length; i++) {
          if( focusableEle[i].offsetWidth || focusableEle[i].offsetHeight || focusableEle[i].getClientRects().length ) {
            firstFocusable = focusableEle[i];
            break;
          }
        }

        return firstFocusable;
      }

      function isVisible(element) {
        return (element.offsetWidth || element.offsetHeight || element.getClientRects().length);
      }

      function doneResizing() {
        if( !isVisible(menuTrigger) && Util.hasClass(flexHeader[0], 'f-header--expanded')) {
          menuTrigger.click();
        }
      }

      function toggleMenuNavigation(bool) { // toggle menu visibility on small devices
        Util.toggleClass(document.getElementsByClassName('f-header__nav')[0], 'f-header__nav--is-visible', bool);
        Util.toggleClass(flexHeader[0], 'f-header--expanded', bool);
        menuTrigger.setAttribute('aria-expanded', bool);
        if(bool) firstFocusableElement.focus(); // move focus to first focusable element
        else if(focusMenu) {
          focusMenu.focus();
          focusMenu = false;
        }
      }
    }